
import React from "react";
import styles from "./RandomItemSetup.module.scss";
import { Button, Checkbox, IconButton, } from "@mui/material";
import RemoveCircleIcon from '@mui/icons-material/RemoveCircle';
import AddCircleIcon from '@mui/icons-material/AddCircle';

export default function RandomItemSetup(props) {

  const isSubmitEnabled = () => {
    const checkCount = props.checked.filter(x => x).length
    return checkCount >= 2
    && (props.type !== 'priorityRule' || (props.data.limit <= checkCount - 1
      && props.data.limit >= 1))
  }

  const onSubmit = (checked) => {
    const selected = checked.filter(x => x).length;
    if (selected < 2) {
      return false;
    }
    if (props.type === 'priorityRule') {
      const submitData = { ...props.data };
      const weightsData = checked.reduce((sum, el, i) => {
        if (el) {
          sum.push(submitData.weights[i]);
        }
        return sum;
      }, []);
      props.onSubmit({
        ...submitData,
        weights: weightsData,
      });
    } else {
      const submitList = checked.reduce((sum, el, i) => {
        if (el) {
          sum.push(props.data[i]);
        }
        return sum;
      }, []);
      props.onSubmit(submitList);
    }
  }

  const decreaseLimit = (limit) => {
    if (limit === 1) {
      return false;
    }
    limit--;
    props.changeLimit(limit);
  };

  const increaseLimit = (limit) => {
    if (limit === props.data.weights.length - 1) {
      return false;
    }
    limit++;
    props.changeLimit(limit);
  };

  const decreaseWeight = (e, i) => {
    if (e === 1) {
      return false;
    }
    e--;
    props.changeWeight(e, i);
  };

  const increaseWeight = (e, i) => {
    e++;
    props.changeWeight(e, i);
  };

  return (
    <div className={styles.RandomisationItem}>
      <div className={`${styles.randomHeader} ${styles.randomTitleWrapper}`}>
        {props.icon ? props.icon : null}
        <h4 className={styles.title}>
          {props.title}
        </h4>
      </div>
      <div className={styles.randomBody}>
        {props.type === 'priorityRule' &&
          <PriorityLimitController
            increaseLimit={increaseLimit}
            decreaseLimit={decreaseLimit}
            limit={props.data.limit} />
        }
        <ul className={`${styles.list} ${styles.active}`}>
          {props.type === 'priorityRule' ?
            props.data && props.data.weights && props.data.weights.map((nestedItem, index) => (
              <PrioritisedChildDisplay
                key={index}
                code={nestedItem.code}
                weight={nestedItem.weight}
                handleChange={(checked) => props.handleChange(checked, index)}
                decreaseWeight={(weight) => decreaseWeight(weight, index)}
                increaseWeight={(weight) => increaseWeight(weight, index)}
                checked={props.checked?.[index]}
                label={props.getChildLabelByCode(nestedItem.code)}
              />
            ))
            :
            props.data && props.data.map((code, index) => (
              <RandomisedChildDisplay
                key={index}
                code={code}
                handleChange={(checked) => props.handleChange(checked, index)}
                checked={props.checked?.[index]}
                label={props.getChildLabelByCode(code)}
              />
            ))
          }
        </ul>
      </div>

      <div className={styles.randomAction}>
        <Button variant="text" onClick={e => props.onCancel()}>
          Cancel
        </Button>
        <Button variant="contained" onClick={e => onSubmit(props.checked)} disabled={!isSubmitEnabled()}>
          Submit
        </Button>
      </div>
    </div>
  );
}

function PrioritisedChildDisplay({ code, checked, label, weight, handleChange, increaseWeight, decreaseWeight }) {
  return <li className={styles.listItem}>
    <Checkbox checked={checked} onChange={e => handleChange(e.target.checked)} />
    <div className={`${styles.content} ${styles.label}`}>
      {code}: {label}
    </div>
    <div className={styles.weight}>
      <IconButton size="small" onClick={e => decreaseWeight(weight)}>
        <RemoveCircleIcon />
      </IconButton>
      <span>{weight}</span>
      <IconButton size="small" onClick={e => increaseWeight(weight)}>
        <AddCircleIcon />
      </IconButton>
    </div>
  </li>;
}

function RandomisedChildDisplay({ code, label, checked, handleChange }) {
  return <li className={styles.listItem}>
    <div className={styles.label}>
      <Checkbox checked={checked} onChange={e => handleChange(e.target.checked)} />
      <div className={`${styles.content} ${styles.label}`}>
        {code}: {label}
      </div>
    </div>
  </li>;
}

function PriorityLimitController({ limit, increaseLimit, decreaseLimit }) {
  return <p>
    Show
    <IconButton size="small" onClick={e => decreaseLimit(limit)}>
      <RemoveCircleIcon />
    </IconButton>
    {limit}
    <IconButton size="small" onClick={e => increaseLimit(limit)}>
      <AddCircleIcon />
    </IconButton>
  </p>
}
