import React from "react"
import styles from "./ImageChoiceQuestion.module.scss"
import { useSelector } from "react-redux"
import {
  GridContextProvider,
  GridDropZone
} from "react-grid-dnd";
import ImageChoiceQuestionItem from "design/ImageChoiceQuestionItem";
import { useTheme } from "@emotion/react";
import { onDrag } from "design/state/designState";
import { useDispatch } from "react-redux";

function ImageChoiceQuestion(props) {
  const theme = useTheme()
  const dispatch = useDispatch()


  const children = useSelector((state) => {
    return state.designState[props.code].children || []
  });

  const questionType = useSelector((state) => {
    return state.designState[props.code].type
  });

  const childrenWithAdd = [...children, { type: "add", code: "add" }]


  const columnNumber = useSelector((state) => {
    return state.designState[props.code].columns || 2
  });

  const imageHeight = useSelector((state) => {
    return state.designState[props.code].imageHeight || 300
  });

  const spacing = useSelector((state) => {
    return state.designState[props.code].spacing || 8
  });


  const totalHeight = Math.ceil(((children ? children.length : 0) + 1) / columnNumber) * (imageHeight + 80 + 2 * spacing)

  const onChange = (sourceId, sourceIndex, targetIndex, targetId) => {
    if (sourceIndex < childrenWithAdd.length - 1 && targetIndex < childrenWithAdd.length - 1) {
      const payload = {
        type: "reorder_answers",
        id: children[sourceIndex].qualifiedCode,
        fromIndex: sourceIndex,
        toIndex: targetIndex
      }
      dispatch(onDrag(payload))
    }

  }


  return (
    <div className={styles.questionItem} style={{
      direction: "ltr"
    }}>

      <GridContextProvider onChange={onChange}>
        <GridDropZone
          id={"items-" + props.code}
          boxesPerRow={columnNumber}
          rowHeight={imageHeight + 80 + 2 * spacing}
          style={{ height: totalHeight + "px" }}
        >
          {childrenWithAdd.map((item) => (
            <ImageChoiceQuestionItem
              key={item.code}
              spacing={spacing}
              addAnswer={() => props.addNewAnswer(props.code, questionType)}
              type={item.type}
              imageHeight={imageHeight}
              qualifiedCode={item.qualifiedCode}
            />
          ))}
        </GridDropZone>
      </GridContextProvider>


    </div >
  )
}

export default ImageChoiceQuestion
