import React from "react";
import Switch from "@mui/material/Switch";
import styles from "./ShowHint.module.scss";
import TextField from "@mui/material/TextField";
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";
import { changeAttribute, changeContent } from "design/state/designState";

function ShowHint({ code }) {
  const dispatch = useDispatch()
  const label = { inputProps: { "aria-label": "Switch demo" } };

  const languagesList = useSelector((state) => {
    return state.designState.langInfo.languagesList
  });

  const showHint = useSelector((state) => {
    return state.designState[code].showHint || false
  });

  const hintObj = useSelector((state) => {
    return state.designState[code].content?.hint
  });

  const setCheckedHint = (value) => {
    dispatch(changeAttribute({ code, key: "showHint", value }))
  }


  const setHintValue = (lang, value) => {
    dispatch(changeContent({ code, key: "hint", lang, value }))
  }
  return <>
    <div className={styles.showHint}>
      <h4>Show Question Hint</h4>
      <Switch
        {...label}
        checked={showHint}
        onChange={(event) => setCheckedHint(event.target.checked)}
      />
    </div>
    {showHint && languagesList.map((lang) => {
      return <div className={styles.inputValue} key={lang.code}>
        <TextField
          label={lang.langName}
          variant="standard"
          type="text"
          value={hintObj?.[lang.code] || ""}
          onChange={(event) => setHintValue(lang.code, event.target.value)}
        />
      </div>
    })
    }
  </>
}

export default ShowHint
