
import React from "react";
import styles from "./RandomItemDisplay.module.scss";
import { IconButton } from "@mui/material";
import ModeIcon from '@mui/icons-material/Mode';
import DeleteOutlineIcon from '@mui/icons-material/Delete';

export default function RandomItemDisplay(props) {

  return (
    <div className={styles.RandomisationItem}>
      <div className={styles.randomHeader}>
        <div className={styles.randomTitleWrapper}>
          {props.icon ? props.icon : null}
          <h4 className={styles.title}>
            {props.title}
          </h4>
        </div>
        <div className={styles.action}>
          <IconButton onClick={() => props.onEditClicked(props.index)}>
            <ModeIcon className={styles.settingIcon} />
          </IconButton>
          <IconButton onClick={() => props.onDeleteClicked(props.index)}>
            <DeleteOutlineIcon className={styles.settingIcon} />
          </IconButton>
        </div>
      </div>
      <div className={styles.randomBody}>
        {props.type === 'priorityRule' && <p>Show {props.data.limit} from {props.data.weights?.length}</p>}
        <ul className={styles.list}>
          {props.type === 'priorityRule' ?
            props.data && props.data.weights && props.data.weights.map((nestedItem, index) => {
              return <PrioritisedChildDisplay
                label={props.getChildLabelByCode(nestedItem.code)}
                code={nestedItem.code}
                childrenCodes={props.childrenCodes}
                weight={nestedItem.weight}
                key={index}
              />
            })
            :
            props.data && props.data.map((code, index) => {
              return <RandomisedChildDisplay
                label={props.getChildLabelByCode(code)}
                code={code}
                childrenCodes={props.childrenCodes}
                key={index}
              />
            })
          }
        </ul>
      </div>
    </div>
  );
}

function RandomisedChildDisplay({ code, label, childrenCodes }) {
  return (<li className={childrenCodes.indexOf(code) !== -1 ? styles.listItem : styles.listItemError}>
      <div className={`${styles.content} ${styles.label}`}>
        {code}: {label}
      </div>
  </li>);
}

function PrioritisedChildDisplay({ code, label, weight, childrenCodes }) {
  return (<li className={childrenCodes.indexOf(code) !== -1 ? styles.listItem : styles.listItemError}>
    <div className={`${styles.content} ${styles.label}`}>
      {code}: {label}
    </div>
    <div className={styles.weight}>
      <span>{weight}</span>
    </div>
  </li>);
}
