import FieldSize from "design/setup/FieldSize";
import ShowHint from "design/setup/ShowHint";
import RandomSetup from "design/setup/random/RandomSetup";
import ValidationSetupItem from "design/setup/validation/ValidationSetupItem";
import React from "react";
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import ToggleValue from "../ToggleValue";
import SelectValue from "../SelectValue";
import SelectDate from "../SelectDate";
import Theming from "../Theming"
import Relevance from "../logic/Relevance";
import SkipLogic from "../SkipLogic";
import styles from "./SetupPanel.module.scss"
import { Accordion, AccordionDetails, AccordionSummary } from "@mui/material";
import { useDispatch } from "react-redux";
import { setupToggleExpand } from "design/state/designState";
import RandomError from "../random/RandomError";
import { instructionByCode } from "design/utils/designUtils";
import { useSelector } from "react-redux";


function SetupPanel({
  setupInfo
}) {

  const dispatch = useDispatch()
  const toggleExpand = (key) => {
    dispatch(setupToggleExpand(key))
  }

  const state = useSelector((state) => {
    return state.designState[setupInfo.code]
  });



  const isSingleRule = setupInfo.rules?.length === 1

  const generateSetupSection = (rule) => {
    return <Accordion
      expanded={setupInfo.expanded?.includes(rule.key) || isSingleRule || false}
      key={setupInfo.code + rule.title}
      TransitionProps={{ unmountOnExit: true }}>
      <AccordionSummary
        sx={{ backgroundColor: '#ECECEC', margin: 0 }}
        onClick={() => toggleExpand(rule.key)}
        className={styles.setupHeader}
        expandIcon={isSingleRule ? null : <ExpandMoreIcon />}
      >
        <span className={styles.sectionTitle}>{rule.title.replace("title:", "")}</span>
      </AccordionSummary>
      <AccordionDetails sx={{ backgroundColor: rule.key == setupInfo.highlighted ? 'beige' : 'background.paper' }}>
        {rule.rules.map((rule) => (
          generateSetupcomponent(rule)
        ))}
      </AccordionDetails>
    </Accordion>

  }

  const generateSetupcomponent = (rule) => {
    if (rule.startsWith("validation_")) {
      return <ValidationSetupItem
        rule={rule}
        key={setupInfo.code + rule}
        code={setupInfo.code}
      />
    }

    switch (rule) {
      case "theme":
        return (
          <Theming
            key={setupInfo.code + rule} />
        );
      case "maxChars":
        return (
          <FieldSize
            label="Text Field Size"
            rule={rule}
            lowerBound={1}
            upperBound={500}
            defaultValue={20}
            code={setupInfo.code}
            key={setupInfo.code + rule}
          />
        );
      case "minRows":
        return (
          <FieldSize
            label="TextArea Lines"
            lowerBound={1}
            upperBound={500}
            code={setupInfo.code}
            defaultValue={20}
            key={setupInfo.code + rule}
          />
        );
      case "showWordCount":
        return (
          <ToggleValue
            key={setupInfo.code + rule}
            label="Show Word Count"
            rule={rule}
            code={setupInfo.code}
          />
        );
      case "hint":
        return (
          <ShowHint
            key={setupInfo.code + rule}
            code={setupInfo.code}
          />
        );

      case "fullDayFormat":
        return (
          <ToggleValue
            key={setupInfo.code + rule}
            rule={rule}
            code={setupInfo.code}
            label="FullDay Format"

          />
        );
      case "maxDate":
        return (
          <SelectDate
            lowerBound={1}
            code={setupInfo.code}
            key={setupInfo.code + rule}
            label="Max Date"
            rule={rule}
          />
        );
      case "minDate":
        return (
          <SelectDate
            key={setupInfo.code + rule}
            label="Min Date"
            rule={rule}
            code={setupInfo.code}
          />
        );
      case "dateFormat":
        const listDateFormat = ['DD.MM.YYYY', 'MM.DD.YYYY', 'YYYY.MM.DD', 'DD/MM/YYYY', 'MM/DD/YYYY', 'YYYY/MM/DD'];
        return (
          <SelectValue
            values={listDateFormat}
            key={setupInfo.code + rule}
            defaultValue='DD.MM.YYYY'
            label="Date Format"
            rule={rule}
            code={setupInfo.code}
          />
        );
      case "columns":
        const columnsOptions = ['1', '2', '3', '4', '6'];
        return (
          <SelectValue
            values={columnsOptions}
            key={setupInfo.code + rule}
            defaultValue='2'
            label="Columns Number"
            rule={rule}
            code={setupInfo.code}
          />
        );
      case "imageHeight":
        return (
          <FieldSize
            label="Image Height"
            lowerBound={50}
            upperBound={500}
            code={setupInfo.code}
            defaultValue={250}
            rule={rule}
            key={setupInfo.code + rule}
          />
        );

      case "spacing":
        return (
          <FieldSize
            label="Spacing"
            lowerBound={2}
            upperBound={32}
            code={setupInfo.code}
            defaultValue={8}
            rule={rule}
            key={setupInfo.code + rule}
          />
        );
      case "random":
        return (
          <RandomSetup
            key={setupInfo.code + rule}
            label=""
            code={setupInfo.code}
          />
        );
      case "random_array":
        const randomInstruction = instructionByCode(state, "random_group")
        const priorityInstruction = instructionByCode(state, "priority_groups")
        const randomErrors = randomInstruction?.errors || []
        const priorityErrors = priorityInstruction?.errors || []
        return <div key={setupInfo.code + rule}>
          {randomErrors.length > 0 ? <RandomError key="randomErrors" errors={randomErrors} /> : ""}
          {priorityErrors.length > 0 ? <RandomError key="priorityErrors" errors={priorityErrors} /> : ""}
          <RandomSetup
            key="rows"
            label="Randomize Rows"
            type="row"
            hideErrors
            code={setupInfo.code}
          />
          <RandomSetup
            key="columns"
            hideErrors
            label="Randomize Columns"
            type="column"
            code={setupInfo.code}
          />
        </div>
      case "skip_logic":
        return (
          <SkipLogic
            key={setupInfo.code + rule}
            code={setupInfo.code}
          />
        );
      case "relevance":

        return <Relevance
          key={setupInfo.code + rule}
          code={setupInfo.code} />
      default:
        return "";
    }
  }


  return (
    <>
      {setupInfo.rules.map((rule, index) => (
        generateSetupSection(rule, index)
      ))}

    </>
  );
}

export default SetupPanel;

