import { FormControl, MenuItem, Select } from "@mui/material"
import { changeAttribute, changeTimeFormats } from "design/state/designState";
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";
import styles from "./SelectValue.module.scss"

function SelectDate({ label, rule, defaultValue, code, values }) {


  const dispatch = useDispatch()

  const value = useSelector((state) => {
    return state.designState[code][rule] || defaultValue
  });

  const onChange = (value) => {
    if (rule == "dateFormat") {
      dispatch(changeTimeFormats({ code, key: rule, value }))
    } else {
      dispatch(changeAttribute({ code, key: rule, value }))
    }
  }



  return <div className={styles.selectDate}>
    <h4>{label}</h4>
    <FormControl variant="standard" fullWidth  >
      <Select
        id="select-value"
        value={value}
        label="Select Value"
        onChange={(e) => { onChange(e.target.value) }}
      >
        {values.map(element => {
          return <MenuItem key={element} value={element}>{element}</MenuItem>
        })}
      </Select>
    </FormControl>
  </div>
}

export default SelectDate
