import React, { useState } from "react";
import styles from "./RandomSetup.module.scss";
import { Button, Stack } from "@mui/material";
import ShuffleIcon from '@mui/icons-material/Shuffle';
import LowPriorityIcon from '@mui/icons-material/LowPriority';

import RandomItemSetup from "../RandomItemSetup";
import RandomItemDisplay from "../RandomItemDisplay";
import { instructionByCode } from "design/utils/designUtils";
import RandomError from "../RandomError";
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";
import { updatePriority, updatePriorityByType, updateRandom, updateRandomByType } from "design/state/designState";


export default function RandomSetup({
  type,
  label,
  code,
  hideErrors
}) {
  const dispatch = useDispatch();
  const [action, setAction] = useState({});
  const [checked, setChecked] = useState([]);


  const state = useSelector((state) => {
    return state.designState[code]
  });

  const childrenCodes = useSelector((state) => {
    return state.designState[code]?.children
      ?.filter(el => type ? el.type == type : true)
      ?.map(el => el.code) || []
  });

  const lang = useSelector((state) => {
    return state.designState.langInfo.lang
  });


  const childrenLabels = useSelector((state) => {
    return state.designState[code]
      ?.children
      ?.filter(el => type ? el.type == type : true)
      ?.map(child => {
        return { code: child.code, label: state.designState[child.qualifiedCode].content?.label?.[lang] }
      }) || []
  });

  const randomInstruction = instructionByCode(state, "random_group")
  const priorityInstruction = instructionByCode(state, "priority_groups")
  const randomOrders = randomInstruction?.groups?.filter(group => type ? group.some(code => childrenCodes.includes(code)) : true) || []
  const priorities = priorityInstruction?.priorities?.filter(priority => type ? priority.weights.some(el => childrenCodes.includes(el.code)) : true) || []
  const randomErrors = randomInstruction?.errors || []
  const priorityErrors = priorityInstruction?.errors || []

  const updatePriorities = priorities => { dispatch(type ? updatePriorityByType({ code, priorities, type }) : updatePriority({ code, priorities })) }
  const updateRandomOrders = groups => { dispatch(type ? updateRandomByType({ code, groups, type }) : updateRandom({ code, groups })) }

  const getChildLabelByCode = code => {
    const label = childrenLabels.find(el => el.code == code)?.label
    return label?.replace(/<[^>]*>/g, "");
  };

  const getUnrandomisedChildrenCodes = (data) => {
    let flatData = data.flat()
    return childrenCodes.filter(el => !flatData.includes(el))
  };

  const getUnPrioritisedChildrenCodes = (data) => {
    let flatData = data.map((priority) => priority.weights).flat().map((weight) => weight.code).flat()
    return childrenCodes.filter(el => !flatData.includes(el))
  };

  const onDeleteClicked = (type, index) => {
    if (window.confirm(`Are you sure?`)) {
      if (type === 'priorityRule') {
        updatePriorities(priorities.filter((el, i) => i !== index));
      } else if (type === 'randomRule') {
        updateRandomOrders(randomOrders.filter((el, i) => i !== index));
      }
    }
  }

  const sortRandomItem = (item) => {
    return [...item].sort(function (a, b) { return childrenCodes.findIndex(x => x === a) - childrenCodes.findIndex(x => x === b) });
  }
  const sortPriorityItem = (item) => {
    let newWeights = [...item.weights].sort(function (a, b) { return childrenCodes.findIndex(x => x === a.code) - childrenCodes.findIndex(x => x === b.code) });
    return { ...item, weights: newWeights }
  }

  const onEditClicked = (type, index) => {
    let checked = [];
    let data = [];
    if (type === 'priorityRule') {
      data.weights = []
      const unPrioritised = getUnPrioritisedChildrenCodes(priorities);
      childrenCodes.forEach(code => {
        if (unPrioritised.includes(code)) {
          data.weights.push({ "code": code, weight: 1 })
          checked.push(false)
        } else {
          let weightIndex = priorities[index].weights.findIndex(weight => weight.code == code)
          if (weightIndex > -1) {
            data.weights.push(priorities[index].weights[weightIndex])
            checked.push(true)
          }
        }
        data.limit = priorities[index].limit
      })
    } else {
      let unrandomized = getUnrandomisedChildrenCodes(randomOrders)
      data = childrenCodes.filter(el => unrandomized.includes(el) || randomOrders[index].includes(el))
      checked = data.map(el => randomOrders[index].includes(el) ? true : false);
    }
    setChecked(checked);
    setAction({
      type: type,
      index: index,
      title: type === 'priorityRule' ? 'Priority Rule' : 'Random Rule',
      data: data,
    });
  }

  const handleCheckboxChange = (e, i) => {
    const cloneChecked = [...checked];
    cloneChecked[i] = e;
    setChecked([...cloneChecked]);
  };

  const resetData = () => {
    setAction({});
    setChecked([]);
  };

  const onSubmit = (value, type, index) => {
    if (type === 'randomRule') {
      const newRandomOrders = randomOrders ? [...randomOrders] : [];
      newRandomOrders[index] = [...value];
      updateRandomOrders(newRandomOrders);
    } else {
      const newPriorities = priorities ? [...priorities] : [];
      newPriorities[index] = { ...value };
      updatePriorities(newPriorities);
    }
    resetData();
  };

  const addNewRule = () => {
    const data = getUnrandomisedChildrenCodes(randomOrders);
    setChecked(data.map(el => false));
    setAction({
      title: '+ Random Rule',
      type: "randomRule",
      data: data,
      index: randomOrders ? randomOrders.length : 0,
      icon: <ShuffleIcon />,
    });
  }

  const addNewPriority = () => {
    const listQuestionCode = getUnPrioritisedChildrenCodes(priorities);
    const weights = listQuestionCode.map(el => ({
      code: el,
      weight: 1,
    }));
    setChecked(listQuestionCode.map(el => false));
    setAction({
      title: '+ Priority Rule',
      type: "priorityRule",
      data: {
        limit: 1,
        weights: weights,
      },
      index: priorities ? priorities.length : 0,
      icon: <LowPriorityIcon />,
    });
  };

  const changeLimit = (limit) => {
    setAction({
      ...action,
      data: {
        ...action.data,
        limit: limit,
      },
    })
  };

  const changeWeight = (e, i) => {
    const data = { ...action.data };
    data.weights = [...data.weights];
    data.weights[i] = { ...data.weights[i], weight: e };
    setAction({
      ...action,
      data: data,
    })
  };

  const sanitize = (data, childrenCodes, type) => {
    if (type === "randomRule") {
      let groups = [...data].filter(code => childrenCodes.includes(code))
      if (groups.length <= 1) {
        resetData()
      }
      return groups
    } else {
      let weights = [...data.weights].filter(el => childrenCodes.includes(el.code))
      if (weights.length <= 1) {
        resetData()
      }
      return { limit: Math.min(data.limit, weights.length - 1), weights: weights }
    }
  }

  return (<div className={styles.Randomisation}>
    {label && <h4 >{label}</h4>}
    {randomOrders && randomOrders.map((item, i) => {
      return <RandomItemDisplay
        key={`randomOrder-${i}`}
        title="Randomize Order"
        data={sortRandomItem(item)}
        index={i}
        childrenCodes={childrenCodes}
        getChildLabelByCode={getChildLabelByCode}
        onDeleteClicked={(index) => { setAction({}); onDeleteClicked("randomRule", index) }}
        onEditClicked={(index) => onEditClicked("randomRule", index)}
        icon={<ShuffleIcon />}
      />
    }
    )}
    {(!hideErrors && randomErrors.length > 0) ? <RandomError key="randomErrors" errors={randomErrors} /> : ""}
    {priorities && priorities.map((item, i) => {
      return <RandomItemDisplay
        key={`prioritize-${i}`}
        title="Prioritize"
        type="priorityRule"
        data={sortPriorityItem(item)}
        index={i}
        childrenCodes={childrenCodes}
        onDeleteClicked={(index) => { setAction({}); onDeleteClicked("priorityRule", index) }}
        onEditClicked={(index) => onEditClicked("priorityRule", index)}
        getChildLabelByCode={getChildLabelByCode}
        icon={<LowPriorityIcon />}
      />
    }
    )}
    {(!hideErrors && priorityErrors.length > 0) ? <RandomError key="priorityErrors" errors={priorityErrors} /> : ""}
    <Stack direction="row" spacing={1.7} sx={{ mt: '40px' }}>
      <Button variant="contained" disabled={getUnrandomisedChildrenCodes(randomOrders).length < 2} onClick={addNewRule}>
        <ShuffleIcon sx={{ paddingRight: '8px' }} /> + Random Rule
      </Button>
      <Button variant="contained" disabled={getUnPrioritisedChildrenCodes(priorities).length < 2} onClick={addNewPriority}>
        <LowPriorityIcon sx={{ paddingRight: '8px' }} /> + Priority Rule
      </Button>
    </Stack>
    {action && action.data &&
      <div className={styles.actionWrapper}>
        <RandomItemSetup
          title={action.title}
          type={action.type}
          icon={action.icon}
          data={sanitize(action.data, childrenCodes, action.type)}
          index={action.index}
          getChildLabelByCode={getChildLabelByCode}
          checked={checked}
          handleChange={handleCheckboxChange}
          changeWeight={changeWeight}
          changeLimit={changeLimit}
          onSubmit={(data) => onSubmit(data, action.type, action.index)}
          onCancel={resetData}
        />
      </div>
    }
  </div>);
}
